@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
}

body {
  height: 100vh;
  width: 100%;
  background: #CCC;
  position: absolute;
}


.task-list {
  margin: 120px auto;
  max-width: 400px;
  width: 100%;
  background: #fff;
  border-radius: 5px;
  padding: 25px;
}

.task-list header{ 
    font-size: 30px;
    font-weight: 600;
}


.container_media{
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background-color: #1C1C1C;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  }
  .container_media p{
    font-size: 1.2rem;
    color:  #fff ;
    margin-left: 20px;
  }
  .container_media a{
    text-decoration: none;
    color: #fff;
  } 
  
  .container_media ul{  
    display:flex;
    margin-right: 50px;
  }
  
  .container_media li{  
    list-style: none;
    margin: 0 15px;
  }


.form-task{
  display: flex;
  height: 45px;
  width: 100%;
  margin: 20px 0;
}

.input-task{
outline: none;
width: 85%;
height: 100%;
border: 1px solid rgb(200, 255, 0);
font-size: 17px;
border-radius: 3px;
padding-left: 15px;
}

.form-task button{
  outline: none;
  border: none;
}

.add-icon{
  width: 60px;
  height: 100%;
  background: #019c28;
  color: #fff;
  border-radius: 3px;
  margin-left: 5px;
  cursor: pointer;
  opacity: 0.6;
  padding:12px;
  stroke-width: 1;
  pointer-events: none;
}

.form-task .add-icon.active {
  opacity: 1;
  pointer-events: auto;
}

.container-task-list{
    max-height: 250px;
    overflow-y: auto;
}

  .task-container{
  width: 349px;
  min-height: 45px;
  background: #f2f2f2;
  margin: 8px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 3px;

  }

.task.text{
  width: 100%;
  height: 100%;
  line-height: 45px;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}


.delete-icon{
  width: 25px;
  height: 25px;
  margin: 5px;
  margin-left: 30px;
  }

/* task done */
.task-container.done {
background-color: #128c7e;
text-decoration: line-through;
}

